import React, { ReactElement } from "react";
import styled from "styled-components";

import { colors, displayFontSizes, spacingMap } from "../styleguide";
import uuid from "../utils/uuid";

import { InputValidation, StyledRequirementProps } from "./Input.types";
import { transition } from "./SharedWithTextArea.styles";


export const InputRequirement = styled.div<StyledRequirementProps>`
	display: block;
	color: ${({ validation }) => {
		if (validation === "valid") return colors.green;
		if (validation === "invalid") return colors.destructive;
		return `${colors.black}A8`;
	}};
	transition-duration: ${transition.duration};
	transition-timing-function: ${transition.function};
	margin-top: ${spacingMap.xxs};
	height: 16px;
	font-family:  ${displayFontSizes.caption.$fontFamily};
	font-size: ${displayFontSizes.caption.$fontSize};
	line-height: ${displayFontSizes.caption.lineHeight};
	font-weight: ${displayFontSizes.caption.$fontWeight};

	animation: input-assistive-text-slide-in .175s cubic-bezier(0.215, 0.61, 0.355, 1) forwards;
	@keyframes input-assistive-text-slide-in {
		0% {
			transform: translateY(-40%);
			opacity: 0;
		}

		100% {
			opacity: 1;
		}
	}
`;

export const InputRequirementList = styled.ul`
	margin: 0;
	padding-left: 20px;
`;
export const buildRequirements =
	(requirements: string[] | undefined, dataQA: string, id: string, validation: InputValidation | undefined) => {
		let requirementItems: string[] = [];
		let requirementItemsList: ReactElement[] = [];

		if (requirements) {
			requirementItems = [...requirementItems, ...requirements];
		}

		if (requirementItems.length > 1) {
			requirementItemsList = requirementItems.map((element) => <li key={uuid()}>{element}</li>);
		}

		const requirementList = (requirementItems.length > 1)
			? <InputRequirementList>{requirementItemsList}</InputRequirementList>
			: requirementItems[0];

		return (
			<InputRequirement
				data-qa={`${dataQA}-requirements`}
				id={`${id}-requirements`}
				validation={validation}>
				{requirementList}
			</InputRequirement>
		);
	};

export const buildAriaLabel = (label: string | undefined, requirements: string[] | undefined, dataQA: string) => {
	let ariaLabelledBy = "";
	if (label && requirements) {
		ariaLabelledBy = `${dataQA}-label ${dataQA}-requirements`;
	} else if (label) {
		ariaLabelledBy = `${dataQA}-label`;
	} else if (requirements) {
		ariaLabelledBy = `${dataQA}-requirements`;
	}
	return ariaLabelledBy;
};
